import {React} from 'react';
import '../styles/App.css';
import certificate from '../assets/icons/certificate.png';

const Experience = ({isMobile}) => {

    const subBox = (title, content) => (
        <div className='experience-sub-box' style={{marginLeft: 10}}>
            <div style={{width: 'fit-content', marginRight: 10}}>
                <img src={certificate} alt='certificate' width={30} height={30}/>
            </div>
            <div style={{width: '40%', display: 'flex', flexDirection: 'column', textAlign: 'left'}}>
                <text className='experience-sub-box-title' style={{fontSize: isMobile ? 12 : 16}}>{title}</text>
                <text className='experience-sub-box-content' style={{fontSize: isMobile ? 8 : 14}}>{content}</text>
            </div>
        </div>
    );

    return (
        <div className='experience-page' style={{alignContent: isMobile ? 'flex-start' : 'center'}}>
            <text className='experience-subtitle'>Em quais tecnologias eu possuo</text>
            <text className='experience-title'>Experincia</text>
            <div style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
                flexWrap: 'wrap'
            }}>
                <div className='experience-box'>
                    <text className='experience-box-title' style={{fontSize: isMobile ? 10 : 20}}>Frontend</text>
                    <div style={{display: 'flex', flexDirection: 'row', flexWrap: 'wrap'}}>
                        {subBox('HTML', 'Alto')}
                        {subBox('CSS', 'Alto')}
                        {subBox('JavaScript', 'Alto')}
                        {subBox('TypeScript', 'Alto')}
                        {subBox('ReactJS', 'Alto')}
                        {subBox('Java Swing', 'Alto')}
                        {subBox('SASS', 'Bsico')}
                    </div>
                </div>
                <div className='experience-box'>
                    <text className='experience-box-title'>Backend</text>
                    <div style={{display: 'flex', flexDirection: 'row', flexWrap: 'wrap'}}>
                        {subBox('Java', 'Alto')}
                        {subBox('Spring', 'Alto')}
                        {subBox('Python', 'Alto')}
                        {subBox('NodeJS', 'Alto')}
                        {subBox('Kotlin', 'Alto')}
                        {subBox('C++', 'Alto')}
                        {subBox('C#', 'Intermedirio')}
                    </div>
                </div>
                <div className='experience-box'>
                    <text className='experience-box-title'>Mobile</text>
                    <div style={{display: 'flex', flexDirection: 'row', flexWrap: 'wrap'}}>
                        {subBox('React Native', 'Alto')}
                        {subBox('Android Studio', 'Alto')}
                        {subBox('QT', 'Alto')}
                        {subBox('Flutter', 'Intermedirio')}
                        {subBox('.NET', 'Intermedirio')}
                    </div>
                </div>
                <div className='experience-box'>
                    <text className='experience-box-title'>Outros</text>
                    <div style={{display: 'flex', flexDirection: 'row', flexWrap: 'wrap'}}>
                        {subBox('Spring', 'Alto')}
                        {subBox('Kafka', 'Mdio')}
                        {subBox('Flask', 'Mdio')}
                        {subBox('Docker', 'Mdio')}
                        {subBox('PostgreSQL', 'Alto')}
                        {subBox('MySQL', 'Alto')}
                        {subBox('MySQL', 'Alto')}
                        {subBox('Firebase', 'Alto')}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Experience;